.text {
  width: 460px;
  margin-left: 10px;
}
.formPopup {
  position: absolute;
  margin-left:650px;
  top: 50px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #111;
}

.active {
  background-color: #4CAF50;
}

.secret {
  margin: 5px;
  padding: 4px;
  color: black;
  border: solid 1px black;
  background-color: lightgrey; 
  text-decoration: none;
}

input {
  margin: 5px;
}  